import { useCallback } from 'react';
import RenderIfVisible from 'react-render-if-visible';

export const ResponsiveImage = ({ srcPrefix, srcExtension, alt, widths, alwaysLoad }: ResponsiveImageProps) => {
  const srcSet = widths.map(width => `${srcPrefix}-${width}${srcExtension} ${width}w`).join(',');
  const sizes = `${widths.slice(0, -1).map(width => `(max-width: ${width}px) ${width}px`).join(',')}, ${widths[widths.length - 1]}px`;
  const Img = useCallback(() => (
    <img
      src={`${srcPrefix}-${widths[widths.length - 1]}${srcExtension}`}
      alt={alt}
      srcSet={srcSet}
      sizes={sizes}
    />
  ), [srcPrefix, widths, srcExtension, alt, srcSet, sizes]);
  return alwaysLoad ? <Img /> : (
    <RenderIfVisible stayRendered visibleOffset={500}>
      <Img />
    </RenderIfVisible>
  );
};

interface ResponsiveImageProps {
  srcPrefix: string,
  srcExtension: string,
  alt: string,
  widths: number[],
  alwaysLoad?: boolean
}
