import { resetSettlementParameters } from '../home/new-settlement/newSettlementFormParameters';
import { useFolderStore } from '../home/folders/folderStore';
import { resetSettlements } from '../home/settlement-list/settlementStore';
import { resetPresetsForGenerationStore } from '../home/new-settlement/presetsStore';

export const clearUserCache = () => {
  resetPresetsForGenerationStore();
  resetSettlementParameters();
  resetSettlements();
  useFolderStore.getState().reset();
};
